import { useDispatch } from "react-redux";

import {
  setCAAllPlants,
  setCAHeaders,
  setCAItemDetails,
  setCAName,
  setHeaders,
  setIsMFGCA,
  setParentDetailsLoading,
  setPlantObjectData,
  setProductChildren,
  setProposedChanges,
  setCAData,
} from "../store/droppedObjectSlice";
import { MSG_FETCH_CSRF_HEADERS_FAILED } from "../utils/toastMessages";
import useToast from "./useToast";
import { fetchCsrfToken } from "../services/api/PlantAssignment/fetchCsrfService";
import { initWidget } from "../lib/widget";
import { callEnoviaWebService, loadWAFData, fetchData } from "../utils/helpers";
import { getUserGroupCollab } from "../services/api/companion/userGroupService";
import { getAllPlants } from "../services/api/companion/allPlantSevice";
import { fetchCADetails } from "../services/api/companion/CADetailService";
import { fetchAssignedPlants } from "../services/api/companion/assignedPlantService";

const useCompanionWidget = () => {
  const { showErrorToast } = useToast();
  const dispatch = useDispatch();
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;

   let email = window.widget.getValue("email");
  console.log("Email in useCompanionWidget:", email);

  // console.log("Security context using preferences are", securitycontext);

  const handleCompanionWidget = async (collabSpace, state, objectId, type) => {
    try {
      dispatch(setParentDetailsLoading(true));
      const headers = await fetchCsrfToken();
      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return;
      }
      // Step 1: Fetch user group collaboration spaces
      const userGroupCollab = await getUserGroupCollab(
        headers,
        objectId,
        email
      );
      console.log("[ Companion ] User Groups:", userGroupCollab);

      let allCollabSpaces = [...userGroupCollab, collabSpace];
      console.log(
        "[Use Companion] All CollabSpaces:",
        allCollabSpaces
      );

      // Step 2: Fetch all plants if collab spaces exist
      let allPlants = [];
      if (allCollabSpaces.length > 0) {
        allPlants = await getAllPlants(allCollabSpaces, headers, objectId);
        console.log("[Use Companion] All Plants:", allPlants);
      } else {
        console.warn("[Use Companion] No CollabSpaces found.");
      }

      // Step 3: Fetch assigned plants if there are any
      if (allPlants.length > 0) {
        const plants = await fetchAssignedPlants(
          allPlants,
          headers,
          objectId,
          state,
          type
        );
        console.log("[Use Companion]: ", plants);

        if (plants.success) {
          dispatch(setPlantObjectData(plants.data.plantData));
          dispatch(setHeaders(headers));
        } else {
          console.error("Failed to fetch plant data.");
        }
      } else {
        console.warn("[Use Companion] No Plants found.");
      }

      // Step 5: Fetch Change Action details
      const getCaDetails = await fetchCADetails(headers, objectId, state);
      console.log("[Use Companion] CA Details:", getCaDetails);

      if (getCaDetails.success) {
        dispatch(setCAName(getCaDetails.data));
        dispatch(setProposedChanges(getCaDetails.proposedChanges));
        dispatch(setCAData(getCaDetails.CAData));
      }

      console.log("[Companion] All services executed successfully.");
    } catch (error) {
      console.error("[Companion] Error:", error);
      throw error; // Let the caller handle the error
    } finally {
      dispatch(setParentDetailsLoading(false)); // Ensure loading state is reset
    }
  };

  return { handleCompanionWidget };
};

export default useCompanionWidget;

