import { callEnoviaWebService, loadWAFData } from "../../../utils/helpers"
import axios from "axios";

export const updateClasses = async (
    headers,
    allPlants,
    objectId,
    state,
    type
  ) => {
    try {
      const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
      const sLatestID = await getLatestRevision(objectId, type, headers);
      console.log("Hello getLatestRevision is Called", sLatestID)
  
      if (sLatestID !== "") {
        const classificationUrl = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslib/dslib:CategorizationClassifiedItem/${sLatestID}?$mask=dslib:ClassificationAttributesMask`;
  
        const ClassResponse = await callEnoviaWebService("GET", classificationUrl, "", headers);
  
        if (ClassResponse.status && ClassResponse.output) {
          const classificationMembers =
            ClassResponse.output.member[0]?.ClassificationAttributes?.member || [];
  
          // Step 1: Filter and map results
          const results = classificationMembers
                .filter(classItem =>
                    classItem.Attributes.some(
                    attr => attr.name === "PlantAssignmentClass" && attr.value === true
                    )
                )
                .map(classItem => {
                    // Update Seq attribute if found
                    const updatedAttributes = classItem.Attributes.map(attr => {
                        if (attr.name === "Seq") {
                          return {
                            name: attr.name,
                            value: String(Number(attr.value) + 1),
                          };
                        } else if (attr.name === "PlantStatus") {
                          return {
                            name: attr.name,
                            value: "Pending",
                          };
                        } else if(attr.name === "AddSeq" || attr.name === "PlantAssignmentClass"){
                          return {
                            name: attr.name,
                            value: attr.value,
                          };
                        }else {
                          return {
                            name: attr.name,
                            value: "",
                          };
                        }
                      });

                    return {
                    ClassID: classItem.ClassID,
                    Attributes: updatedAttributes,
                    };
                });
  
          console.log("Matching ClassID and Seq:", results);
  
          // Step 2: If results exist, classify and then update
          if (results.length > 0) {
            const classifyUrl = `https://saasimplementationserverdev.azurewebsites.net/plantAssignment/classifyProductToClass`;
  
            const classifyBody = {
              id: objectId,
              type: type,
              classes: results.map(item => item.ClassID),
              mode: "classifyParent",
            };
  
            try {
              const classifyResponse = await fetchData("POST", classifyUrl, classifyBody);
              console.log("classifyResponse:", JSON.stringify(classifyResponse));
  
              // Proceed with second call only after classify completes
              const referencedObject = {
                source: ENOVIA_BASE_URL,
                type: "dslib:CategorizationClassifiedItem",
                identifier: objectId,
                relativePath: `resources/v1/modeler/dslib/dslib:CategorizationClassifiedItem/${objectId}`,
              };
  
              const categorizationAttributes = results.map(res => ({
                classId: res.ClassID,
                attributes: res.Attributes.reduce((acc, attr) => {
                  acc[attr.name] = attr.value;
                  return acc;
                }, {}),
              }));
  
              const payload = [
                {
                  referencedObject,
                  categorizationAttributes,
                },
              ];
  
              console.log("Prepared payload for second API call:", JSON.stringify(payload));
  
              // Second API call
              const secondUrl = `https://saasimplementationserverdev.azurewebsites.net/plantAssignment/updateClassificationAttribute?id=${objectId}`;
              const secondResponse = await fetchData("PATCH", secondUrl, payload);
  
              console.log("Second API call response:", JSON.stringify(secondResponse));
              
              return results;
              // return {
              //   success: true,
              //   classifyResponse,
              //   secondResponse,
              // };
  
            } catch (error) {
              console.error("Error during classify or second API call:", error);
              // return { success: false, error };
              return [];
            }
          } else {
            console.warn("No matching classifications found with Plant = True.");
            // return { success: false, message: "No matching classes to classify." };
            return [];
          }
        } else {
          console.error("Invalid classification response from ENOVIA.");
          // return { success: false, message: "Invalid classification data." };
          return [];
        }
      } else {
        console.warn("No latest revision ID found.");
        // return { success: false, message: "No latest ID." };
        return [];
      }
    } catch (error) {
      console.error("[updateClasses] Failed to process:", error);
      // return { success: false, error };
      return [];
    }
  };
  

async function getLatestRevision(identifier, type, headers) {
    const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
    const revurl = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslc/version/getGraph`;
    let ret = "";
    try {
      const relativePath =
        type === "Raw_Material"
          ? `/resources/v1/modeler/dsrm/dsrm:RawMaterial/${identifier}`
          : `/resources/v1/modeler/dseng/dseng:EngItem/${identifier}`;
  
      const Body = {
        data: [
          {
            id: identifier,
            identifier: identifier,
            type: type,
            source: ENOVIA_BASE_URL,
            relativePath: relativePath,
          },
        ],
      };
  
      // Make the API call with the dynamically created body
      const response = await callEnoviaWebService(
        "POST",
        revurl,
        // JSON.stringify(Body),
        Body,
        headers
      );
  
      // Check if the response contains status and output properties
      if (response.status && response.output) {
        // Loop through each result in the response and check for ancestors
        

        const versions = response.output.results[0].versions;
        
        for (const version of versions) {
            if (version.identifier === identifier) {
                console.log("Found matching version for ID:", identifier);
                if (Array.isArray(version.ancestors) && version.ancestors.length > 0) {
                    ret = version.ancestors[0].id;
                    console.log("Ancestor ID:", ret);
                    break;
                }
            }
        }
        return ret;
      } else {
        console.error(
          "API response does not contain the expected 'status' and 'output'."
        );
        return ret;
      }
    } catch (error) {
      console.error(`Error fetching getLatestRevision for ${identifier}:`, error);
      return ret;
    }
  }

  export const fetchData = async (
    method = "GET",
    url,
    body = null
  
    // headers = {}
  ) => {
    console.log("Fetching URL:", url);
    console.log("Method is:");
    console.log("Request Body:", JSON.stringify(body, null, 2));
  
    try {
      const response = await axios({
        url,
        method,
        // headers: {
        //   ...headers,
        //   "Content-Type": "application/json",
        // },
        data: body, // Include body only if provided
      });
  
      console.log("Response received:", response.data);
      return response;
    } catch (error) {
      console.error("Request failed:", error);
      throw error; // Propagate the error to the caller
    }
  };
